import React, { useRef } from 'react'
import { FcGoogle } from 'react-icons/fc'
import styled from 'styled-components'
import openColor from '../../lib/openColor'

export type GoogleLoginButtonProps = {}

function GoogleLoginButton({}: GoogleLoginButtonProps) {
  const buttonRef = useRef<HTMLButtonElement>(null)
  return (
    <GoogleStyle ref={buttonRef} id="google-login-button">
      <FcGoogle />
      Sign in with Google
    </GoogleStyle>
  )
}

export default GoogleLoginButton

const GoogleStyle = styled.button`
  border: none;
  outline: none;
  background: none;
  padding: 0;
  width: 100%;
  display: inline-flex;
  height: 54px;
  background: white;
  align-items: center;
  justify-content: center;
  padding: 12px 16px;
  font-size: 16px;
  cursor: pointer;
  transition: all ease-in 0.125s;
  border-radius: 7px;
  border: 1px solid ${openColor.gray[3]};
  svg {
    font-size: 24px;
    margin-right: 8px;
  }
  &:hover {
    box-shadow: 0px 0.25rem 0.5rem rgb(0 0 0 / 11%);
  }
`
